import React from "react"
import { Link } from "gatsby"

import Layout from "../components/layout"
import SEO from "../components/seo"

const COfCodeHome = () => (
    <Layout>
        <SEO title="C of Code Home" />
        <Link to="/">Home</Link>
        <h1>C code related articles</h1>
        <p>Select a category</p>
        <p>
            <Link to="/coc_datastructures/">Data Structures</Link>
        </p>
    </Layout>
)

export default COfCodeHome
